<template>
    <div class="detail">
        
        <div class="content_center">
            <div><i class="el-icon-arrow-left" style="font-size: 20px;" @click="handleClickBack"></i></div>
            <div class="title">{{ datas.name }}</div>
            <div class="time">{{ getTimes(datas.time) }}</div>
            <div style="margin-top: 40px;">
                {{ datas.content }}
            </div>
            <div>
                <p style="word-wrap:break-word;margin-top: 50px;">下载地址：<a :href="datas.link">{{ datas.link }}</a></p>
                <p style="word-wrap:break-word;margin-top: 20px;">密码：{{ datas.password }}</p>
            </div>

        </div>
    </div>
</template>

<script>
import { getTime } from '@/tools/tools'
export default {
    name: 'detail',
    components: {
    },
    data() {
        return {
            datas: this.$route.query.rows
        }
    },
    methods: {
        getTimes(time) {
            return getTime(time)
        },
        handleClickBack(){
            this.$router.go(-1)
        }

    }


}
</script>

<style lang="less" scoped>
.detail {
    .content_center{
        width: 95%;
        margin: 0 auto;
        padding-top: 20px;
        .title{
            font-size: 26px;
            text-align: center;
            font-weight: bold;
        }
        .time{
            text-align: center;
        }
    }

}
</style>